.navbar {
background-color: #343a40 ;
height:50px;
width: 100vw;
z-index:1;
display: flex;
flex-direction: row;
justify-content: flex-start;
align-items: center;
position: fixed;


}
.fullNavButton{
    background-color: #4caf4f00;
    width: 80px; 
    height:50px;
    border: none;
    color: white;
    text-align: center;

    

}
.fullNavButton:focus{
    background-color: #4caf4f00;
    width: 80px; 
    border: none;
    color: white;
    text-align: center;
    border-bottom: #dd4124 solid 2px;

}
.fullNavButton:hover{
   transform: translateY(-1px);
   

}
.toggleBtn{
    height:30px;
    width: 40px;
    padding:10px;
   
}
.fullNavBar{
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}
.navButtonContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.navButton{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 50px;
    background-color: transparent;
    border: none;
    align-items: center;
    
}
.navButton:hover{
    background-color: rgb(255, 176, 124);
}
.unionGapLogo{
    display: flex;
    margin-left: 0px;
    margin-right: 0px;
    width:150px;
    margin:20px;
}
.letterHeadFullscreen{
    height:40px;
   position: fixed;
   right:0px;
   margin-right:10px;
  
}
.letterHeadMobile{
    height:50px;
  position: fixed;
   right:0px;
 
   margin-right:9px;
}