   background-size:100vw 65vh;
   background-position:bottom;
   z-index: 0;
   min-height: 450px;

  }

  .donationCardParent{
    width:100vw;
    height:50vh;
    min-width: 360px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
 
