.progress{
    float: left;
  height: 20px;
  background: #dd4124;
  z-index: 1;
}
.thermometer{
    width: 100%;
    height: 20px;
    background: #c7c7c7;
    border-radius: 10px;
    float: left;
    overflow: hidden;
}
.statText{
  margin:5px;
}
.stats{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding:10px;
  border-bottom: black solid 1px;
}
.statsParent{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width:100%;
  justify-content: space-around;
  align-items: flex-start;
  font-family:Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
}
#amountRaised{
  font-family:Georgia, 'Times New Roman', Times, serif;
  font-size: 40px;
  
 z-index: 1;

}
.progressBarContainer{
 width: 90%;
 max-width: 600px;
}