.parentContainerBuilding{
    height: calc(100vh - 50px);
    background-color: rgb(252, 249, 247);
    z-index: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: none;
    justify-content: flex-start;
    align-items: flex-start;

  }
  .carousel{
    height: 100%;
    width: 100%;
    max-width: 100vw;
    height: calc(45vh - 50px);
    min-width:360px;
  }
  .carouselParent{
    top:0px;
    height:calc(45vh - 50px);
    min-width:360px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgb(30, 39, 39);
    z-index:0;

  }
  .carouselParentMobile{
    height:calc(45vh - 50px);
    min-width:360px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(47, 0, 255);
  }
  .image{
    max-width: calc((45vh - 50px) * 1.75);
    height:calc(45vh - 50px);
  }
  .designInfoParent{
    min-width: 360px;
    height:100%;
    width: 100%;
   display: flex;
   flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
   
  }
  .buildingP{

    font-size: min(1.2vw, 16px);
    margin:0px;
    text-align: left;
  }
  .buildingPMobile{

    margin:5px;
    text-align: left;
  }
  .exteriorParent{
    height: 100%;
    width:100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
   text-align: center;
    padding:10px;
   max-width: 500px;
 background-color: rgb(250, 111, 31);
  }
  .floorPlanParent{

    height:100%;
    width:100%;
   display: flex;
   flex-direction: column;
   flex-wrap: none;
   align-items: center;
   justify-content: space-evenly;
   text-align: center;
    padding:10px;
  }
  .floorPlanParentMobile{

    height:100%;
    width:100%;
   display: flex;
   flex-direction: column;
   flex-wrap: none;
   align-items: center;
   justify-content: flex-start;
   text-align: center;
    padding:10px;
    font-family: Cambria,  Times, 'Times New Roman', serif;
 
  }
  .buildingHeader{
    width:100%;
    text-align: center;
  }
  .picDiv{
 
  }
  .smallTextImage{
    height:250px;
  width:auto;
  }
  .downloadLinkContainer{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-right: 70%;
    width:100%;
  }
  .downloadLink{
    margin:5px;
  }
